import React from 'react'
import './index.less'
import NoneData from 'components/NoneData/all-none'
import TitleBar from 'components/topbar'
import connect from 'connect'
import {  Button, WhiteSpace, WingBlank, List, InputItem, Flex, Switch, Picker, Modal, PickerView } from 'antd-mobile';
import { createForm } from 'rc-form';
import districtdata from './district';
@connect
class Home extends React.Component {
    constructor(props){
        super(props);
        this.state = {
          value: null,
          
        }
    }
    
    componentDidMount () {
       
    }

    handleClick = () => {
        this.inputRef.focus();
    }
  onChange = (value) => {
    console.log(value);
    this.setState({
      value,
    });
  }
  onScrollChange = (value) => {
    console.log(value);
  }

  showModal = key => (e) => {
    e.preventDefault(); // 修复 Android 上点击穿透
    this.setState({
      [key]: true,
    });
  }
  onClose = key => () => {
    this.setState({
      [key]: false,
    });
  }


    render () {
        const Item = List.Item;
        const Brief = Item.Brief;
        console.log(districtdata)
        let SwitchExample = (props) => {
          const { getFieldProps } = props.form;
          return (
            <List
            >
            <Picker extra="请选择"
                      data={districtdata}
                      title={'选择地址'}
                      {...getFieldProps('district', {
                        initialValue: ['340000', '341500', '341502'],
                      })}
                      onOk={e => console.log('ok', e)}
                      onDismiss={e => console.log('dismiss', e)}
                    >
                      <List.Item arrow="horizontal">选择地区</List.Item>
                    </Picker>
              <List.Item
                extra={<Switch
                  {...getFieldProps('Switch1', {
                    initialValue: true,
                    valuePropName: 'checked',
                  })}
                  onClick={(checked) => { console.log(checked); }}
                />}
              >是否设置为默认地址</List.Item>
            </List>
          );
        };
        SwitchExample = createForm()(SwitchExample);
        return (
            <div className="editaddress-container">
            <TitleBar title="新增地址"/>
            <List>
                <InputItem
                  clear
                  placeholder="请输入姓名"
                  ref={el => this.inputRef = el}
                >姓名</InputItem>
                <InputItem
                  clear
                  placeholder="请输入电话"
                  ref={el => this.inputRef = el}
                >联系电话</InputItem>

                <SwitchExample/>
                
            </List>
             
            <div className="cardfooter">
                <Button>新增地址</Button>
            </div>
     </div>
        )
    }
}
export default Home


